.banner {
    color: white;
    object-fit: contain;
    height: 448px; 
}

.banner__contents {
    margin-left: 30px; 
    padding-top: 140px;
    height: 190px;
}

.banner__title {
    font-size: 3rem;
    font-weight: 800;
    margin-bottom: 0.3rem;
}

.banner__description {
    font-size: 1rem;
    line-height: 1.3;
    width: 45rem;
    padding-top: 1rem;
    max-width: 360px;
    height: 80px; 

}

.banner__button {
    cursor: pointer;
    color: #fff;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 0.2vw;
    padding-left: 2rem;
    padding-right: 2rem;
    margin-right: 1rem;
    padding-top: 0.5rem;
    background-color: rgba(51, 51, 51, 0.5);
    padding-bottom: 0.5rem;

}

.banner__button:hover {
    color: #000;
    background-color: #e6e6e6;
    transition: all 0.25;
}

.banner--fadeBottom {
    height: 7.4rem;
    background-image: linear-gradient(
        180deg, 
        transparent, 
        rgba(37, 37, 37, 0.61), 
        #111
        );
} 
